import React from "react";
import PropTypes from "prop-types";
import { Switch, Route } from "react-router-dom";

import { makeStyles } from "@material-ui/core/styles";
import Events from "./containers/EventsBlog";
import Event from "./containers/EventBlog";

import events from "data/blogging.json";


const Blogging = ({ onToggleMenu }) => {
  
  return (
    
    <Switch>
      <Route exact path="/blog">
        <Events
          onToggleMenu={onToggleMenu}
          logo={events.logo}
          title={events.title}
          description={events.description}
          events={events.items}
          seoKey="blogging"
        />
      </Route>
      <Route path="/blog/:event">
        <Event onToggleMenu={onToggleMenu} onClosePath="/" />
      </Route>
    </Switch>
  );
};
Blogging.propTypes = {
  onToggleMenu: PropTypes.func,
};
export default Blogging;